}
.fa-github {
  color: #131418;
}
.fa-twitter {
  color: #55acee;
}
.fa-linkedin {
  color: #0077b5;
}
.fa-instagram {
  color: #df2029;
}
.fa-coffee {
  color: #4e342e;
}
.fa {
  margin: 7px;
  transition: 0.4s all ease-in;
  text-shadow: 0px 0px 0px white;
}
.fa:hover {
  cursor: pointer;
}
.fa-facebook:hover,
.fa-google-plus:hover,
.fa-skype:hover,
.fa-github:hover,
.fa-twitter:hover,
.fa-linkedin:hover {
  transform: rotate(360deg);
}
.fa-arrow-up {
  transition: 0.2s all ease-in;
}
.fa-arrow-up:hover {
  transform: scale(1.05);
}
